import React from 'react';
import './Header.css';
import { Link } from 'react-scroll';
import { FaBars } from "react-icons/fa";


export default function HeaderComponent () {

  function burgerToggle() {
    let linksEl = document.querySelector('.narrowLinks');
    if (linksEl.style.display === 'block') {
      linksEl.style.display = 'none';
    } else {
      linksEl.style.display = 'block';
    }
  }
    

  return(

    <div className="Header-component">
      <nav>
				<div className="navWide">
        <img src="https://i.ibb.co/cv8ctvr/Matt-Profile-Img.jpg" alt="Matt" className="Profile-small"></img>
					<div className="wideDiv">
            <div className="Tool-button"><Link  to="about" spy={true} activeClass="active" smooth={true}>About</Link></div>
            <div className="Tool-button"><Link  to="projects" spy={true} activeClass="active" smooth={true}>Projects</Link></div>
            <div className="Tool-button"> <Link  to="contact" spy={true} activeClass="active" smooth={true}>Contact</Link></div>
					</div>
				</div>
				<div className="navNarrow">
        <img src="https://i.ibb.co/cv8ctvr/Matt-Profile-Img.jpg" alt="Matt" className="Profile-small"></img>
					<FaBars className="FaBars" onClick={burgerToggle} size="25px" />
					  <div className="narrowLinks">
              <li><Link  to="about" spy={true} activeClass="active" smooth={true} onClick={burgerToggle}>About</Link></li>
						  <li><Link  to="projects" spy={true} activeClass="active" smooth={true} onClick={burgerToggle}>Projects</Link></li>
              <li><Link  to="contact" spy={true} activeClass="active" smooth={true} onClick={burgerToggle}>Contact</Link></li>
            </div>
				  </div>
			  </nav>
        {/* <div className="Tool-button"><Link  to="about" spy={true} activeClass="active" smooth={true}>About</Link></div>
        <div className="Tool-button"><Link  to="projects" spy={true} activeClass="active" smooth={true}>Projects</Link></div>
        <div className="Tool-button"><Link  to="contact" spy={true} activeClass="active" smooth={true}>Contact</Link></div> */}
    </div>

    
  )
}
